<!--题目管理-->
<template>
  <div class="home">
    <div class="nav-select">
      <div class="nav-left">
        <el-button type="primary" icon="el-icon-plus" @click="addSubBtn">
          新 增
        </el-button>
      </div>
      <div class="nav-right">
        <div class="nav-left-second">
          <el-select v-model="classfyId" placeholder="请选择筛选科目">
            <el-option label="全部" value="0">全部</el-option>
            <el-option label="数学" value="2">数学</el-option>
            <el-option label="政治" value="3">政治</el-option>
            <el-option label="数据结构" value="4">数据结构</el-option>
            <el-option label="计组" value="5">计组</el-option>
            <el-option label="计网" value="6">计网</el-option>
          </el-select>
        </div>
        <div class="nav-left-second">
          <el-select v-model="coursId" placeholder="请选择筛选题目来源">
            <el-option label="全部" value="0">全部</el-option>
            <el-option label="考研真题" value="1">考研真题</el-option>
            <el-option label="408" value="2">408</el-option>
            <el-option label="自主出题" value="3">自主出题</el-option>

          </el-select>
        </div>
        <el-button type="primary" class="searchData" @click="getSearchBtn">查 询</el-button>
      </div>
    </div>

    <!--主题-->
    <el-table :data="tableData" style="width: 100%">
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column prop="questionId" label="ID" width="100"></el-table-column>
      <el-table-column prop="sites" label="题目考点"></el-table-column>
      <el-table-column prop="classifyName" label="题目类型"></el-table-column>
      <el-table-column prop="difficult" label="题目难度"></el-table-column>
      <el-table-column prop="source" label="题目来源"></el-table-column>
      <el-table-column prop="year" label="题目年份"></el-table-column>
      <el-table-column label="操作" width="150">
        <template slot-scope="scope">
          <el-button type="success" icon="el-icon-check" circle size="mini" @click="lookQuestionRow(scope.row)"></el-button>
          <el-button type="primary" icon="el-icon-edit" circle size="mini"  @click="editRow(scope.row)"></el-button>
          <el-button type="danger" icon="el-icon-delete" circle size="mini" @click="deleteQuestionRow(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--分页表单-->
    <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize"
      @current-change="handleCurrentChange" class="page">
    </el-pagination>

    <!--提示框的组件-->
    <QuestionPopup ref="QuestionPopup"></QuestionPopup>
    <!--显示题目详情的组件-->
    <showQuestionMorePopup ref="showQuestionMorePopup"></showQuestionMorePopup>
  </div>
</template>

<script>
import GiveRole from "../../components/permissManage/GiveRole.vue";
import QuestionPopup from '@/components/popUp/questionPopup.vue';

import { getQuestionList,deleteQuestionItem } from '../../services/questionManage'
import ShowQuestionMorePopup from '@/components/popUp/showQuestionMorePopup.vue';

export default {
  name: "questionManage",
  data() {
    return {
      searchForm: {
        // 查询条件
        key: "",
      },
      //根据后端所川回的值
      tableData: [],
      total: 0, //页面总条数
      page: 1, //请求第一次的params
      pageSize: 0,
      classfyId: "",          //题目类型
      coursId:"",
    };
  },
  components: { GiveRole, QuestionPopup, ShowQuestionMorePopup },
  mounted() {
    this.getQuestionData({classfyId:0,coursId:0,pageNum:1});
  },
  methods: {
    //获取所有列表的信息
    getQuestionData({classfyId,coursId,pageNum}) {
      getQuestionList({classfyId, coursId, pageNum }).then((res) => {
        console.log(res);
        this.tableData = res.data.data.list;
        this.total = res.data.data.total; //后端返回的总数量
        this.pageSize = res.data.data.pageSize
      })
    },










    //分页
    handleCurrentChange(page) {
      this.page = page;
      this.getQuestionData({pageNum:this.page});
    },

    //查看具体的题目
    lookQuestionRow(row) {
      this.$refs.showQuestionMorePopup.showQuestionMore(row)
      console.log(row);
    },
    //删除对应的题目 
    deleteQuestionRow(row){
      console.log(row);
      deleteQuestionItem(row.questionId).then((res)=>{
        if(res.data.code === 200){
          this.$message.success('删除成功')
          //分页器返回到第1页 
          this.page = 1
          //获取对应的相关数据 
          this.getQuestionData({classfyId:0,coursId:0,pageNum:1})
        }
      })
    },
    //根据相应的条件查询出对应的题目内容
    getSearchBtn() {
      let classfyId = this.classfyId === "" ? 0 : this.classfyId
      let coursId = this.coursId === "" ? 0 : this.coursId
      console.log(classfyId,coursId);
      this.getQuestionData({classfyId,coursId,pageNum:this.page})
    },
    //添加题目的弹窗 
    addSubBtn() {
      console.log(this.$refs);
      this.$refs.QuestionPopup.showDialogVisible();
    },
  },
};
</script>

<style scoped lang="less">
.nav-select {
  margin: 10px 20px;
  display: flex;
  justify-content: space-between;

  .nav-right {
    display: flex;
    .nav-left-second{
      margin-right: 10px;
    }
    .searchData {
      margin-left: 10px;
    }
  }
}

.page {
  text-align: center;
  margin-top: 30px;
}
</style>


